* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  height: 100%;
  font-family: Arial, sans-serif;
}

#background-wrapper {
  background: linear-gradient(to right, #74ebd5, #acb6e5);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  display: flex;
}

h1 {
  text-align: center;
  color: #333;
  margin-top: .75em;
  margin-bottom: .25em;
  font-size: 2em;
}

h2 {
  text-align: center;
  color: #333;
  margin: 0;
  font-size: 1.6em;
}

p {
  text-align: center;
  max-width: 1000px;
  margin: 10px 0;
}

#gameCanvas {
  border: 5px solid #333;
  width: 100%;
  max-width: 1000px;
  min-height: 600px;
  margin: 0;
}

@media only screen and (width <= 768px) {
  #gameCanvas, #instructions {
    display: none;
  }

  #mobileMessage {
    text-align: center;
    color: #333;
    border: 5px solid #333;
    margin: 10px;
    padding: 20px;
    font-family: Courier New, Courier, monospace;
    font-size: 18px;
    display: block;
  }
}

@media only screen and (width >= 769px) {
  #mobileMessage {
    display: none;
  }

  #instructions {
    display: block;
  }
}

